import { Component, OnInit } from '@angular/core'
import {MatDialog} from '@angular/material/dialog';

import { ApiService } from '../apiService.service'
import { RestaurantDto } from '../class/RestaurantDto'
import { SpotDetail } from '../class/SpotDetail'
import { TitleDto } from '../class/TitleDto'
import { RestaurantComponent } from '../component/restaurant.component'

@Component({
  selector: 'com-detail',
  templateUrl: './detail.component.html',
})
export class DetailComponent implements OnInit {
  restaurantList: RestaurantDto[] =[]
  spot: SpotDetail = {
    id: '',
    name: '',
    imgUrlList: [],
    city: '',
    address: '',
    zipCode: '',
    websiteUrl: '',
    descriptionDetail: '',
    openTime: '',
    phone: '',
    typeList: [],
    updateTime: '',
    ticketInfo: '',
    position: {
      lat: 0,
      lon: 0,
    },
    parkingInfo: '',
  }
  infoList: TitleDto[] = [
    {
      type: 'text',
      title: '地址',
      iconClass: 'icon-address',
      key:'address'
    },
    {
      type: 'text',
      title: '停車資訊',
      iconClass: 'icon-car',
      key:'parkingInfo'
    },
    {
      type: 'text',
      title: '營業時間',
      iconClass: 'icon-date',
      key:'openTime'
    },
    {
      type: 'text',
      title: '服務電話',
      iconClass: 'icon-phone',
      key:'phone'
    },
    {
      type: 'text',
      title: '費用',
      iconClass: 'icon-dollar',
      key:'ticketInfo'
    },
    {
      type: 'link',
      title: '官網',
      iconClass: 'icon-website',
      key:'websiteUrl'
    },
  ]

  constructor(
    private apiService: ApiService,
    public dialog: MatDialog
    ) {}
  ngOnInit(): void {
    this.apiService.getSpotDetailById('123').subscribe((d) => {
      this.spot = d
      this.restaurantList= new Array(4).fill({
        id:'1',
        name:'田雞小館',
        imgUrl: d.imgUrlList[0].url,
        position:100,
        address:d.address,
        openTime:d.openTime,
        description:d.descriptionDetail
      })
    })
  }

  public openFoodDialog(food:RestaurantDto):void{
    const dialogRef = this.dialog.open(RestaurantComponent, {
      width: '880px',
      data:food,
      panelClass:['cus-model']
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
}


