import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../apiService.service';
import { SpotSimple } from '../class/SpotSimple';

@Component({
  selector: 'com-search',
  templateUrl: './search.component.html',
})
export class SearchComponent implements OnInit{
  public resultList:SpotSimple[] = []; 
  public pageNumList: number[] = [0,1,2,3,4]
  public currentPage: number = 0;
 constructor(
   private apiService:ApiService,
   private router: Router
 ){
 
 }

 ngOnInit(): void {
  this.apiService.getSpotlight().subscribe(d=>{
    this.resultList = d;
  })
}
  public viewDetail(spotId:string):void{
    window.open(`detail/${spotId}`, "_blank");
  }
}
