import { Route } from '@angular/compiler/src/core';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../apiService.service';
import { SpotSimple} from '../class/SpotSimple'
@Component({
  selector: 'com-home',
  templateUrl: './home.component.html',
})
export class HomeComponent implements OnInit{
 public spotlightList:SpotSimple[] = [];
 public filterList: RecommendFilterDto[]=[];
constructor(
  private apiService:ApiService,
  private router: Router
){

}
  ngOnInit(): void {
    this.apiService.getSpotlight().subscribe(d=>{
      this.spotlightList = d;
      this.filterList = new Array(6).fill({
        imgUrl: this.spotlightList[0].img.url,
        filterParam: ''
      })
    })
  }
  callApi():void{
    this.apiService.testApi().subscribe(d=>{
      console.log(d)
    })
  }
  public viewDetail(spotId:string):void{
    window.open(`detail/${spotId}`, "_blank");
  }

  public goFilter(filterData:any):void{
    this.router.navigate(['search'])
  }
}



