import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { Observable, of } from 'rxjs'
import * as hmacsha1 from 'hmacsha1'
import { SpotDetail } from './class/SpotDetail'
import { SpotSimple } from './class/SpotSimple'
import { SpotGroupType } from './class/SpotGroupType'

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  constructor(private http: HttpClient) {}

  testApi(): Observable<any> {
    return this.getApiByUrl(
      `https://ptx.transportdata.tw/MOTC/v2/Tourism/ScenicSpot?$top=30&$format=JSON`,
    )
  }

  public getSpotlight(): Observable<any> {
    return of(spotList)
  }

  public getSpotDetailById(id: string): Observable<SpotDetail> {
    return of(fakeDetail)
  }
  private getApiByUrl(url: string): Observable<any> {
    return this.http.get(url, { headers: this.getHeaders() })
  }

  private getHeaders(): HttpHeaders {
    const gmtTime = new Date().toUTCString()
    const hash = hmacsha1('XnkO4Q6oiRxv8Q7LfXsO-E_ECug', `x-date: ${gmtTime}`)
    const headers: HttpHeaders = new HttpHeaders({
      'Content-Type': 'application/json',
      Authorization: `hmac username="924170cd868348de90cf7b30f8cda570", algorithm="hmac-sha1", headers="x-date", signature="${hash}"`,
      'x-date': gmtTime,
    })
    return headers
  }
}

